import { RouteProps } from "react-router-dom";
import Dashboard from "../components/Dashboard";
import Post from "../pages/Post";

interface MyRouteProps extends RouteProps {
  name: string;
  label: string;
}

export const routes: MyRouteProps[] = [
  {
    name: "dashboard",
    label: "Dashboard",
    path: "/",
    component: Dashboard,
    exact: true,
  },
  {
    name: "post",
    label: "Post",
    path: "/post",
    component: Post,
    exact: true,
  },
];
