import axios from "axios";

export const apiCategori = axios.create({
  baseURL: process.env.REACT_APP_CATEGORI_URL,
  headers: {
    Authorization: `Bearer${process.env.REACT_APP_POST_URL_TOKEN}`,
  },
});

export const apiPost = axios.create({
  baseURL: process.env.REACT_APP_POST_URL,
});

